/* navbar styling */
.navbar {
  font-family: 'Poppins', sans-serif;
}

.name-logo {
  margin-left: 200px;
  margin-right: 150px;
  padding: 5px;
  max-width: 45%;
  color: darkseagreen;
  font-size: x-large;
}
  
.name-logo:hover {
  color: darkseagreen;
}

.navbar-nav .nav-link {
  max-width: 55%;
  display: inline;
}

#btn {
  flex-direction: row;
}

.btn.btn-primary {
  color: darkseagreen;
  background-color: transparent;
  border: none;
  font-size: large;
  padding: 5px;
  margin: 5px;
}

.btn.btn-primary:hover {
  border: solid 3px darkseagreen;
  background-color: transparent;
  color:darkseagreen;
}

.whole {
  font-family: 'Poppins', sans-serif;
  color: darkseagreen;
}

.profile-pic {
  border-radius: 50%;
  float: left;
  width: 200px;
  padding: 0px;
  margin: 0px;
}

.bio-text{ 
  margin: 10px;
  padding: 10px;
}

.btn.btn-secondary {
  border: none;
  background-color:darkseagreen;
  color: white;
  border-radius: 10px;
}
/* landing styling */
.hero {
  border: 0px;
}

.hero-image {
  width: 1400px;
  height: 700px;
  position: relative;
}

.onHover {
  position: relative;
}

.moreBtn {
  text-align: center;
  background-color: transparent;
  border: none;
  color: darkseagreen;
  margin: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-family: 'Poppins', sans-serif;
}

  
/* middle styling */
.jumbotron {
  background-color:thistle !important; 
  margin-top: 45px;
  height: 250px;
  border: 0px;
  color: white;
  padding-left: 50px;
  font-family: 'Poppins', sans-serif;
  }
/* work grid styling */
.work-grid {
    margin-top: 43px;
    margin-bottom: 10px;
  }
  
  #work {
    margin: 20px;
  }
  
  #work:hover {
    border: solid 5px darkseagreen;
  }
  
/* footer nav */
.footer-nav {
    background-color: darkseagreen; 
    padding: 30px;
    margin-top: 20px;
    min-width: 100%;
    margin: 0;
    font-family: 'Poppins', sans-serif;
    color: white;
  }
  
body {
  color: darkseagreen;
  font-family: 'Poppins', sans-serif;
  background-repeat: no-repeat;
  background-attachment: fixed; 
}


